.comm-container {
    height: calc(100vh - 120px);
    margin-top: 75px;
}

.comm-part {
    height: calc(100vh - 240px);
    overflow: auto;
}

.comtable {
    background: #ffffff;
    padding: 20px;
    border-radius: 8px;
}

.comm-part table tr th {
    background: #ed8b00;
    color: #ffffff;
    position: sticky;
    box-shadow: 0 2px 2px -1px rgba(0, 0, 0, 0.4);
    top: 0;
}

.comm-part table tr td,
.comm-part table tr th {
    vertical-align: middle;
}

.comm-part table tr td:nth-child(1) {
    color: #007bff !important;
}

.comm-part table tr td:nth-last-child(1),
.comm-part table tr td:nth-last-child(2),
.comm-part table tr th:nth-last-child(1),
.comm-part table tr th:nth-last-child(2) {
    text-align: center;
}

.p-btn {
    font-family: gothambook, sans-serif;
    background: #fff;
    padding: 14px 65px;
    border-radius: 25px;
    font-size: 20px;
    font-weight: 700;
}

.center-head {
    position: relative;
}

.backBtn {
    color: #007bff;
    text-decoration: underline;
    font-weight: bold;
}

.iconquestion {
    position: absolute;
    right: 0;
    top: 40%;
    cursor: pointer;
    transform: translate(0, -40%);
}

.pdf-icon {
    color: #e94333;
    font-size: 27px;
    margin-top: 4px;
}

.comm-part table tbody tr:nth-child(even) {
    background: #f6f7f9;
}

@media screen and (max-width: 767px) {
    .comm-part table tr td {
        min-width: 200px;
    }

    .p-btn {
        font-family: gothambook, sans-serif;
        background: #fff;
        padding: 8px 20px;
        border-radius: 25px;
        font-size: 15px;
        font-weight: 700;
    }

    .comm-container {
        margin-top: 20px;
        padding: 10px;
    }
}

.em-pt span {
    font-weight: bold;
}

.comm-part .table th {
    border-top: none;
}